<template>
  <div class="banner mb-5">
    <div class="intro-container row justify-content-md-center">
      <div class="pt-5 col intro-text">
        <h2 class="intro-title mb-4 fw-bold">
          동네 이웃들이 <br />
          자주 가는 동네 가게
        </h2>
        <p class="fs-5 mb-4 fw-lighter">
          우리 동네 사람들이 <br />
          이용하는 가게를 찾아보세요.
        </p>
      </div>
      <div class="col img-size">
        <img
          class="img-fluid"
          src="https://www.daangn.com/_next/static/media/nearby_stores_hero_image_3x.47c43e04.png"
        />
      </div>
    </div>
  </div>
  <div class="container">
    <div class="custom row">
      <h1 class="text-center mb-5 mt-5 fw-bold">
        다양한 동네가게를 볼 수 있어요
      </h1>
      <div
        @click="goToDetailPage(i)"
        v-for="(data, i) in NearbyData"
        :key="i"
        class="col-md-6 hovers"
      >
        <div class="mb-4 pb-4 border-bottom">
          <div class="d-flex align-items-center">
            <div class="pe-3" style="height: 5rem; width: 6rem">
              <img
                :src="NearbyData[i].image"
                class="card-img-top rounded-circle image-thumbnail"
                alt="카드 이미지1"
              />
            </div>
            <div class="card-body">
              <p class="card-text">
                {{ data.store }}
                {{ data.address1 }}
              </p>
              <p class="card-text fs-6">{{ truncatedString(data.content) }}</p>
              <p class="card-text">
                <span class="fw-lighter">후기 {{ data.review }} · </span>
                <span class="fw-lighter"
                  >단골 {{ data.regularCustomer }} ·
                </span>
                <span class="fw-lighter">{{ data.category }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NearbyData from "../nearby-storesdata";
export default {
  data() {
    return {
      NearbyData,
      maxLength: 20,
    };
  },
  methods: {
    goToDetailPage(id) {
      this.$router.push(`/nearby-post/${id}`);
    },
    truncatedString(contents) {
      if (contents.length <= this.maxLength) {
        return contents;
      } else {
        const truncated = contents.substring(0, this.maxLength);
        return truncated + (truncated.endsWith(" ") ? "" : "..."); // 공백을 끝에 추가하지 않음
      }
    },
  },
};
</script>

