<template>
  <div class="container mt-5">
    <div class="custom">
      <div class="mb-3 image-wrap">
        <img
          :src="JobData[id].image"
          alt="카드 이미지"
          class="card-img-top rounded-4 img-fluid"
        />
      </div>
      <div class="d-flex align-items-center mb-2 pb-3 border-bottom">
        <div id="profile-img" class="flex-shrink-0">
          <img
            src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-profiles/avatar-2.webp"
            alt="Generic placeholder image"
            class="img-fluid rounded-circle border border-dark border-3"
            style="width: 100%"
          />
        </div>
        <div class="flex-grow-1 ms-3">
          <div class="d-flex flex-row align-items-center">
            <p class="mb-0 me-2 fs-6">{{ JobData[id].name }}</p>
          </div>
          <div class="d-flex flex-row align-items-center mb-2">
            <p class="mb-0 me-2 fw-lighter fs-7">
              {{ JobData[id].address1 }}
            </p>
          </div>
        </div>
        <div class="profile-right">
          <div class="flex-row align-items-center">
            <p
              class="mb-0 fs-6 fw-bold text-end"
              :class="{
                'gray-text': JobData[id].ondo <= 12.5,
                'blue-text': JobData[id].ondo > 12.5 && JobData[id].ondo <= 30,
                'skyblue-text':
                  JobData[id].ondo > 30 && JobData[id].ondo <= 36.5,
                'green-text':
                  JobData[id].ondo > 36.5 && JobData[id].ondo <= 50.5,
                'yellow-text':
                  JobData[id].ondo > 50.5 && JobData[id].ondo <= 65.6,
                'orange-text':
                  JobData[id].ondo > 65.6 && JobData[id].ondo <= 100,
              }"
            >
              {{ JobData[id].ondo }}°C
            </p>
            <div class="progress" style="width: 150px; height: 5px">
              <div
                class="progress-bar"
                role="progressbar"
                :class="{
                  'bg-gray': JobData[id].ondo <= 12.5,
                  'bg-blue': JobData[id].ondo > 12.5 && JobData[id].ondo <= 30,
                  'bg-skyblue':
                    JobData[id].ondo > 30 && JobData[id].ondo <= 36.5,
                  'bg-green':
                    JobData[id].ondo > 36.5 && JobData[id].ondo <= 50.5,
                  'bg-yellow':
                    JobData[id].ondo > 50.5 && JobData[id].ondo <= 65.6,
                  'bg-orange':
                    JobData[id].ondo > 65.6 && JobData[id].ondo <= 88,
                }"
                :style="{ width: progressWidth }"
              ></div>
            </div>
          </div>
          <div class="flex-row mb-2 text-end">
            <p class="mb-0 fw-lighter fs-7">매너 온도</p>
          </div>
        </div>
      </div>
      <div class="card-body">
        <div class="mt-5 mb-5">
          <h5 class="card-title fs-4">{{ JobData[id].title }}</h5>
          <div class="mt-5 mb-5 lh-base">
            <p class="card-text fs-5">정보</p>
            <p class="card-text fs-5 fw-bold pt-3 pb-3">
              시급: {{ format(JobData[id].wage) }}원
            </p>
            <p class="card-text fs-6 pb-2">주소: {{ JobData[id].address1 }}</p>
            <p class="card-text fs-6 pb-2">근무날짜: {{ JobData[id].week }}</p>
            <p class="card-text fs-6 pb-2">근무시간: {{ JobData[id].time }}</p>
          </div>
          <div class="mt-5 mb-5">
            <p class="card-text fs-5 fw-bold mb-2">상세내용</p>
            <p class="card-text mb-3 lh-base">
              {{ JobData[id].content }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import JobData from "../jobsdata";

export default {
  props: ["id"],
  data() {
    return {
      JobData,
    };
  },
  methods: {
    format(price) {
      return new Intl.NumberFormat("ko-KR", {
        style: "currency",
        currency: "KRW",
      }).format(price);
    },
  },
  computed: {
    progressWidth() {
      // Calculate the progress bar width based on FleaMarketData[id].ondo and a maximum value of 100
      const ondo = this.JobData[this.id].ondo;
      return (ondo / 100) * 100 + "%";
    },
  },
};
</script>

