<script>
import NearbyData from "../nearby-storesdata";

export default {
  props: ["id"],
  data() {
    return {
      NearbyData,
      maxLength: 20,
    };
  },
  methods: {
    format(price) {
      return new Intl.NumberFormat("ko-KR", {
        style: "currency",
        currency: "KRW",
      }).format(price);
    },
  },
  computed: {
    progressWidth() {
      // Calculate the progress bar width based on FleaMarketData[id].ondo and a maximum value of 100
      const ondo = this.NearbyData[this.id].ondo;
      return (ondo / 100) * 100 + "%";
    },
  },
};
</script>

