<template>
  <div class="container mt-5">
    <div class="custom">
      <div class="mb-3 image-wrap">
        <img
          :src="FleaMarketData[id].image"
          alt="카드 이미지"
          class="card-img-top rounded-4 img-fluid"
        />
      </div>
      <div class="d-flex align-items-center mb-2 pb-3 border-bottom">
        <div id="profile-img" class="flex-shrink-0">
          <img
            src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-profiles/avatar-2.webp"
            alt="Generic placeholder image"
            class="img-fluid rounded-circle border border-dark border-3"
            style="width: 100%"
          />
        </div>
        <div class="flex-grow-1 ms-3">
          <div class="d-flex flex-row align-items-center">
            <p class="mb-0 me-2 fs-6">{{ FleaMarketData[id].seller }}</p>
          </div>
          <div class="d-flex flex-row align-items-center mb-2">
            <p class="mb-0 me-2 fw-lighter fs-7">
              {{ FleaMarketData[id].address }}
            </p>
          </div>
        </div>
        <div class="profile-right">
          <div class="flex-row align-items-center">
            <p
              class="mb-0 fs-6 fw-bold text-end"
              :class="{
                'gray-text': FleaMarketData[id].ondo <= 12.5,
                'blue-text':
                  FleaMarketData[id].ondo > 12.5 &&
                  FleaMarketData[id].ondo <= 30,
                'skyblue-text':
                  FleaMarketData[id].ondo > 30 &&
                  FleaMarketData[id].ondo <= 36.5,
                'green-text':
                  FleaMarketData[id].ondo > 36.5 &&
                  FleaMarketData[id].ondo <= 50.5,
                'yellow-text':
                  FleaMarketData[id].ondo > 50.5 &&
                  FleaMarketData[id].ondo <= 65.6,
                'orange-text':
                  FleaMarketData[id].ondo > 65.6 &&
                  FleaMarketData[id].ondo <= 100,
              }"
            >
              {{ FleaMarketData[id].ondo }}°C
            </p>
            <div class="progress" style="width: 150px; height: 5px">
              <div
                class="progress-bar"
                role="progressbar"
                :class="{
                  'bg-gray': FleaMarketData[id].ondo <= 12.5,
                  'bg-blue':
                    FleaMarketData[id].ondo > 12.5 &&
                    FleaMarketData[id].ondo <= 30,
                  'bg-skyblue':
                    FleaMarketData[id].ondo > 30 &&
                    FleaMarketData[id].ondo <= 36.5,
                  'bg-green':
                    FleaMarketData[id].ondo > 36.5 &&
                    FleaMarketData[id].ondo <= 50.5,
                  'bg-yellow':
                    FleaMarketData[id].ondo > 50.5 &&
                    FleaMarketData[id].ondo <= 65.6,
                  'bg-orange':
                    FleaMarketData[id].ondo > 65.6 &&
                    FleaMarketData[id].ondo <= 88,
                }"
                :style="{ width: progressWidth }"
              ></div>
            </div>
          </div>
          <div class="flex-row mb-2 text-end">
            <p class="mb-0 fw-lighter fs-7">매너 온도</p>
          </div>
        </div>
      </div>

      <div class="card-body">
        <div class="mt-5">
          <h5 class="card-title">{{ FleaMarketData[id].title }}</h5>
          <p class="card-text fs-6 fw-lighter">
            {{ FleaMarketData[id].category }}
          </p>
          <p class="card-text fs-5 fw-bold mb-4">
            {{ format(FleaMarketData[id].price) }}원
          </p>
          <div>
            <p class="card-text mb-3 lh-base">
              {{ FleaMarketData[id].content }}
            </p>
          </div>
          <p class="card-text">
            <span class="fw-lighter"
              >관심 {{ FleaMarketData[id].likes }} ·
            </span>
            <span class="fw-lighter"
              >채팅 {{ FleaMarketData[id].chat }} ·
            </span>
            <span class="fw-lighter">조회 {{ FleaMarketData[id].views }}</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FleaMarketData from "../fleamarketdata";

export default {
  props: ["id"],
  data() {
    return {
      FleaMarketData,
    };
  },
  methods: {
    format(price) {
      return new Intl.NumberFormat("ko-KR", {
        style: "currency",
        currency: "KRW",
      }).format(price);
    },
  },
  computed: {
    progressWidth() {
      // Calculate the progress bar width based on FleaMarketData[id].ondo and a maximum value of 100
      const ondo = this.FleaMarketData[this.id].ondo;
      return (ondo / 100) * 100 + "%";
    },
  },
};
</script>

