import { createWebHistory, createRouter } from "vue-router";
import fleamarket from './components/FleamarketPage.vue'
import job from './components/JobPage.vue'
import nearbyStore from './components/NearbyPage.vue'
import fleamarketdetail from "./components/FleamarketDetail.vue";
import jobdetail from "./components/JobDetail.vue";
import nearbydetail from "./components/NearbyDetail.vue"
import Main from './components/MainPage.vue';
const routes = [
  {
    path : '/',
    component : Main
  },
  {
    path: "/fleamarket",
    component : fleamarket
  },
  {
    path : "/nearby-stores",
    component : nearbyStore
  },
  {
    path : "/jobs",
    component : job
  },
  {
    path : "/articles/:id",
    component : fleamarketdetail,
    props : true
  },
  {
    path :"/job-post/:id",
    component :jobdetail,
    props :true
  },
  {
    path :"/nearby-post/:id",
    component :nearbydetail,
    props :true
  }

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 